.main-header {
  @include flex-row;
  justify-content: space-between;
  align-items: flex-start;
  color: $text-color-light;

  min-height: var(--header-min-height);
  width: 100%;

  padding: 0 calc(((100vw - var(--width)) / 2) + var(--gap));

  position: absolute;
  top: 30px;
}

.main-header__logo {
  padding-top: 5px;

  &:hover {
    opacity: 0.8;
  }
}

.main-header__logo-icon {
  fill: $text-color-light;
}

.main-header__cls-btn {
  display: none;
}

.main-header__menu-btn {
  display: none;
  border: none;
  background-color: transparent;
}

.main-header__nav {
  padding-top: 13px;
}

.main-header__nav-list {
  @include clear-list;
  @include flex-row;
  justify-content: space-between;
}

.main-header__nav-item {
  margin-left: 33px;
}

.main-header__nav-link {
  color: $text-color-light;
  text-decoration: none;
  text-align: center;

  font-size: 16px;
  line-height: 20px;
  font-weight: 500;

  &:hover,
  &:focus {
    opacity: 0.9;
  }

  &:active {
    color: $text-color;
    opacity: 0.75;
  }
}

@media (max-width: $tablet-width-only) {
  .main-header {
    @include container;
  }

  .main-header__logo {
    padding-top: 0;
  }

  .main-header__logo-icon {
    width: 38px;
    height: 20px;
  }

  .main-header__nav {
    margin-top: -2px;
    padding-top: 0;
  }

  .main-header__nav-item {
    margin-left: 24px;
  }

  .main-header__nav-link {
    font-size: 14px;
    line-height: 17px;
  }
}

@media (max-width: $mobile-width-only) {
  .main-header {
    @include flex-column;
    background-color: $text-color-light;
    top: 0;
    min-height: 62px;
  }

  .main-header--no-js {
    background-color: $text-color-light;
    position: relative;
    top: 0;
    padding-bottom: 49px;
    min-height: 299px;
  }

  .main-header--closed {
    @include flex-row;
    top: 20px;
    justify-content: space-between;
    background-color: transparent;
  }

  .main-header--no-js .main-header__menu-btn {
    display: none;
  }

  .main-header--closed .main-header__menu-btn {
    display: block;
    cursor: pointer;
    margin-left: auto;
    margin-right: -6px;
    margin-top: 3px;

    &:hover,
    &:focus {
      opacity: 0.75;
    }

    &:active {
      opacity: 0.5;
    }
  }

  .main-header--closed .main-header__logo {
    margin-top: 0;
    margin-bottom: 11px;
  }

  .main-header__logo {
    margin-top: 20px;
    margin-bottom: 31px;
  }

  .main-header--closed .main-header__cls-btn {
    display: none;
  }

  .main-header__cls-btn {
    position: absolute;
    top: 23px;
    right: 8px;

    display: block;
    border: none;
    background-color: transparent;

    cursor: pointer;

    &:hover,
    &:focus {
      opacity: 0.75;
    }

    &:active {
      opacity: 0.5;
    }
  }

  .main-header__logo-icon {
    fill: $text-color;
    width: 42px;
    height: 22px;
  }

  .main-header--closed .main-header__logo-icon {
    fill: $text-color-light;
  }

  .main-header--no-js .main-header__logo-icon {
    fill: $text-color;
  }

  .main-header__nav-list {
    @include flex-column;
  }

  .main-header--no-js .main-header__nav-list {
    @include flex-column;
  }

  .main-header--closed .main-header__nav-list {
    display: none;
  }

  .main-header__nav-item {
    margin-top: 10px;
    margin-bottom: 9px;

    margin-left: 0;
  }

  .main-header__nav-link {
    font-size: 18px;
    line-height: 22px;

    color: $text-color;
  }
}
